/**
 * Networking Strategies Page JavaScript
 * 
 * This file contains the JavaScript for the Networking Strategies page.
 */

// Import components
import Accordion from '../components/accordion';
import Testimonials from '../components/testimonials';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Networking Strategies page initialized');
  
  // Initialize accordion
  Accordion.init({
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  });
  
  // Initialize testimonials
  Testimonials.init({
    testimonialSelector: '.testimonial',
    dotSelector: '.testimonial-dot',
    activeClass: 'active',
    autoRotate: true,
    autoRotateInterval: 5000
  });
  
  // Set up networking event calendar
  setupEventCalendar();
  
  // Set up networking tracker
  setupNetworkingTracker();
}

/**
 * Set up networking event calendar
 */
function setupEventCalendar() {
  const eventItems = document.querySelectorAll('.networking-event-item');
  
  eventItems.forEach(item => {
    const detailsButton = item.querySelector('.event-details-btn');
    const detailsContent = item.querySelector('.event-details-content');
    
    if (detailsButton && detailsContent) {
      detailsButton.addEventListener('click', function() {
        const isExpanded = detailsContent.classList.contains('active');
        
        // Close all other event details
        document.querySelectorAll('.event-details-content.active').forEach(content => {
          if (content !== detailsContent) {
            content.classList.remove('active');
            content.style.maxHeight = '0';
          }
        });
        
        // Toggle current event details
        detailsContent.classList.toggle('active');
        
        if (!isExpanded) {
          detailsContent.style.maxHeight = detailsContent.scrollHeight + 'px';
        } else {
          detailsContent.style.maxHeight = '0';
        }
      });
    }
  });
}

/**
 * Set up networking tracker
 */
function setupNetworkingTracker() {
  const trackerForm = document.querySelector('.networking-tracker-form');
  
  if (trackerForm) {
    trackerForm.addEventListener('submit', function(e) {
      e.preventDefault();
      
      // Get form data
      const name = trackerForm.querySelector('input[name="contact-name"]').value;
      const company = trackerForm.querySelector('input[name="contact-company"]').value;
      const date = trackerForm.querySelector('input[name="contact-date"]').value;
      const notes = trackerForm.querySelector('textarea[name="contact-notes"]').value;
      
      // Add to tracker list
      addContactToTracker(name, company, date, notes);
      
      // Reset form
      trackerForm.reset();
    });
  }
  
  /**
   * Add contact to networking tracker
   */
  function addContactToTracker(name, company, date, notes) {
    const trackerList = document.querySelector('.networking-tracker-list');
    
    if (trackerList) {
      const contactItem = document.createElement('div');
      contactItem.className = 'networking-contact-item';
      
      contactItem.innerHTML = `
        <div class="contact-header">
          <h4>${name}</h4>
          <span class="contact-company">${company}</span>
          <span class="contact-date">${date}</span>
        </div>
        <div class="contact-notes">${notes}</div>
        <button class="contact-delete-btn" aria-label="Delete contact">
          <i class="fas fa-trash"></i>
        </button>
      `;
      
      // Add delete functionality
      const deleteButton = contactItem.querySelector('.contact-delete-btn');
      deleteButton.addEventListener('click', function() {
        contactItem.remove();
      });
      
      // Add to list
      trackerList.appendChild(contactItem);
    }
  }
}

// Initialize the page when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
